import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    routeName: "tuijian",
    soundSrc: "",
    footerState: true,
    soundList: null,
    // audiodom
    audioplay: null,
    play: false,
    durationchange: null,
    timeupdate: null,
    routerState: 0,
    currentId:3,
    playList: null,
    playSongCont: null,
    downPlayid: null,
    historyRecord: JSON.parse(window.localStorage.getItem("historyRecord")) ?? [],
    //
    defaultPlaybackRate:1,
  },
  //
  getters: {
    changeSoundList: function (state, res) {
      console.log(state, res);
    },
  },
  mutations: {
    //
    //
    emptyPlaylist: function (state, list) {
      state.playList = list;
    },
    //
    nowPlayMusic: function (state, song) {
      state.playSongCont = song;
    },
    //
    changeMusic: function (state, status) {
      state.play = status;
    },
    //
    changeAudioSpeed:function(state,speed){
      console.log(speed);
      state.audioplay.playbackRate = speed
      state.defaultPlaybackRate = speed
      // myVid=document.getElementById("video1");
      // function getPlaySpeed()
      // { 
      //   alert(myVid.defaultPlaybackRate);
      // } 
      // function setPlaySpeed()
      // { 
      //   myVid.defaultPlaybackRate=0.5;
      //   myVid.load();
      // } 
    },
    //
    changePlay: function (state, status) {
      // state.play = status;
      console.log(status);
      if (status) {
        state.audioplay.play();
        state.play = status;
      } else {
        state.audioplay.pause();
        state.play = status;
      }
    },

    //
    changeRouterState: function (state) {
      state.routerState += 1;
    },
    //audiodom
    playMusic(state, el) {
      state.audioplay = el;
    },
    //
    SoundList: function (state, res) {
      state.soundList = res;
    },
    //
    changeName: function (state, str) {
      state.routeName = str;
    },
    //
    changeSoundSrc: function (state, src) {
      // console.log(src);
      state.soundSrc = src;
    },
    //
    duration: function (state, time) {
      state.durationchange = time;
    },
    //
    updateTime: function (state, time) {
      state.timeupdate = time;
    },
    //
    changeMusicTime: function (state, time) {
      state.audioplay.currentTime = time;
    },
    //
    changeState: function (state, res) {
      state.footerState = res;
    },
    //
    storageHistory: function (state, value) {
      // console.log(value);
      state.historyRecord = [...new Set([value, ...state.historyRecord].splice(0, 10))];
      window.localStorage.setItem("historyRecord", JSON.stringify(state.historyRecord));
    },
    //
    clearHistory(state) {
      state.historyRecord = null;
    },
    //
    setCurrentId(state, n) {
      state.currentId = n;
    },
  },
  actions: {},
  modules: {},
});
