import Vue from "vue";
//
const Category = () => Vue.axios.get("https://apis.netstart.cn/ximalaya/search/hotWordBillboardCategory");
//
const boardList = (id) =>
  Vue.axios.get("https://apis.netstart.cn/ximalaya/search/hotWordBillboardList", {
    params: {
      categoryId: id,
      //
      size: 20,
    },
  });
//
const searchEnd = ({ kw, core = "all", page = "1" } = {}) =>
  Vue.axios.get("https://apis.netstart.cn/ximalaya/search", {
    params: {
      kw,
      core,
      page,
      rows: 20,
    },
  });
//tab
const SearchTab = ({ kw } = {}) =>
  Vue.axios.get("https://apis.netstart.cn/ximalaya/search/allCategory", {
    params: {
      kw,
    },
  });

//
const SearchFilterData = ({
  kw,
  core = "all",
  page = "1",
  category_id = "-1",
  condition = "relation",
  paidFilter,
  fqCont,
} = {}) =>
  Vue.axios.get("https://apis.netstart.cn/ximalaya/search", {
    params: {
      kw,
      core,
      page,
      category_id,
      condition,
      paidFilter,
      "fq=is_paid": fqCont,
    },
  });

//
const getCategory = () => Vue.axios.get("https://apis.netstart.cn/ximalaya/category/queryCategories");
const getClassifyMain = ({ categoryCode, subCategoryCode, pageSize = 30, sort = 0 }) =>
  Vue.axios.get("https://apis.netstart.cn/ximalaya/category/queryCategoryPage", {
    params: {
      categoryCode,
      subCategoryCode,
      pageSize,
      sort,
    },
  });

//
export { Category, boardList, searchEnd, SearchTab, SearchFilterData, getCategory, getClassifyMain };
